/****/
.container,body {
  background-color: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  height: 100vh;
  font-family: "Open Sans";
}

.card {
  width: 284px;
  height: auto;
  background: #fff;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  box-shadow: 0 14px 26px rgba(0, 0, 0, 0.04);
  transition: all 0.3s ease-out;
  text-decoration: none;
  margin: 60px;
  padding: 30px;
}

.card p {
  font-size: 17px;
  color: #4c5656;
  margin-top: 20px;
  z-index: 1000;
  transition: color 0.3s ease-out;
  word-wrap: wrap;
  word-break: break-all;
}

.card p a {
  padding-left: 24px;
  padding-right: 10px;
  color: #4c5656;
  display: inline-block;
}


.filter-holder{
  z-index: 10000;
  position:fixed;
  top:-35px;
}



button {
  display: inline-block;
  position: relative;
  padding: 10px 15px;
  margin: 50px;
  color: whitesmoke;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-decoration: none;
}

/* ------------ */
/* OFFSET       */
/* ------------ */

button.offset {
  color: black;
  border: 5px solid black;
}

/* create the offset background */
button.offset:before {
  content: '';
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  z-index: -1;
  /* resposition with transform */
  transform: translate(-10px, 10px);
  background-color: firebrick;
  transition: all 500ms ease;
}

/* when we hover over the button, change the bg*/
button.offset:hover:before {
  background-color: darkorange;
  transform: translate(0,0);
}

/* ------------ */
/* OVERLAP      */
/* ------------ */

button.overlap { 
  background-color: darkslategrey;
  transition: background-color 1s;
}

/* create the two overlap layers */
button.overlap:before,
button.overlap:after {
  content: '';
  position: absolute;
  top: 0; left: 0;
  /* bigger than the actual button 
  because the overlay is an illusion */
  width: calc(100% + 10px); 
  height: calc(100% + 10px);
  z-index: -1;
  transition: 
    all 300ms ease,
    background-color 500ms;

}

/* style and position each */
button.overlap:before {
  background-color: dodgerblue;
  transform: translate(-10px, -10px);
}

button.overlap:after {
  background-color: firebrick;
  transform: translate(0,0);
}

/* change on button hover */
button.overlap:hover { 
  background-color: black;
}

button.overlap:hover:before {
  transform: translate(-5px, -5px);
}

button.overlap:hover:after {
  background-color: black;
  transform: translate(-5px, -5px);
}
